.hero {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: 0.4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1,
.hero h2 {
  color: rgb(255, 255, 255);
}

.hero h1 {
  font-size: 2.5rem;
  padding-top: 140px;
  margin-top: 0;
}

div {
  margin-bottom: 1rem;
}
